import React from 'react';
import {Textceptions} from "./text_tricks";
var moment = require( 'moment' );

export {TimeZoneSelector, ClockSelectable, ClockSelectableTwo};


export default class Clock extends React.Component {
    constructor(props) {
        super(props);
        this.state = {date: new moment(), two: props.two};
    }

    componentDidMount() {
        this.timerID = setInterval(
            () => this.tick(), 1000
        );
    }
    componentWillUnomount() {
        clearInterval(this.timerID);
    }

    tick() {
        this.setState({
            date: new moment()
        });
        console.log(this);
    }

    render() {
        const two = this.state.two;
        return (
            <div>
                {two ? <Textceptions str1={this.state.date.format('HH:mm:ss')} str2={this.state.date.format('HH:mm:ss')} />
                 : <h2 style={{fontFamily: "Roboto Mono", color: "black"}}>{this.state.date.format('HH:mm:ss')}</h2>}
            </div>
            )
    }
}

class TimeZoneSelector extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            gmt_offset: 0
        }
        this.inputUpdated = this.inputUpdated.bind(this);
        this.submitOffset = this.submitOffset.bind(this);
    }
    submitOffset(event) {
        event.preventDefault();
        const { gmt_offset } = this.state;
        const { onSubmit } = this.props
        onSubmit(gmt_offset);
    }
    inputUpdated(event) {
        this.setState({ gmt_offset: event.target.value });
    }
    render() {
        return (
            <form onSubmit={this.submitOffset}>
                <h3>{this.props.title ? this.props.title : "Time Zone Selector"}</h3>
                <label htmlFor="gmt offset">GMT offset in hours </label>
                <input name="gmt_offset" type="number" step="any" value={this.state.gmt_offset} onInput={this.inputUpdated} />
                <button type="submit" className='btn btn-success'>Set Offset</button>
            </form>
        )
    }
}

 class ClockSelectable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {date: moment.utc(), gmt_offset: 0};
        this.onFormSubmit = this.onFormSubmit.bind(this);
    }

    componentDidMount() {
        this.timerID = setInterval(
            () => this.tick(), 1000
        );
    }
    componentWillUnomount() {
        clearInterval(this.timerID);
    }

    createDateAsUTC(date) {
        return new Date(Date.UTC(date.getFullYear(), date.getMonth(), date.getDate(), date.getHours(), date.getMinutes(), date.getSeconds()));
    }

    tick() {
        var new_date = moment.utc();
        console.log("new_date: ", new_date);
        new_date.add(this.state.gmt_offset, 'hours');
        console.log("new_date: ", new_date);
        this.setState({
            date: new_date
        });
        console.log(this);
    }

    onFormSubmit(gmt_offset) {
        this.setState({ gmt_offset: gmt_offset});
    }

    render() {
        return (
            <div>
                <TimeZoneSelector onSubmit={this.onFormSubmit} />
                <h2 style={{color: 'black'}}>{this.state.date.format('HH:mm:ss')}</h2>  
            </div>
        )
    }
}

class ClockSelectableTwo extends React.Component {
    constructor(props) {
        super(props);
        this.state = {date1:  moment.utc(), date2: moment.utc(), gmt_offset1: 0, gmt_offset2: 0};
        this.onFormSubmit1 = this.onFormSubmit1.bind(this);
        this.onFormSubmit2 = this.onFormSubmit2.bind(this);
    }

    componentDidMount() {
        this.timerID = setInterval(
            () => this.tick(), 1000
        );
    }
    componentWillUnomount() {
        clearInterval(this.timerID);
    }

    createDateAsUTC(date) {
        return new Date(Date.UTC(date.getFullYear(), date.getMonth(), date.getDate(), date.getHours(), date.getMinutes(), date.getSeconds()));
    }

    tick() {
        let new_date1 = moment.utc();
        let new_date2 = moment.utc();
        new_date1.add(this.state.gmt_offset1, 'hours');
        new_date2.add(this.state.gmt_offset2, 'hours');

        this.setState({
            date1: new_date1,
            date2: new_date2
        });
    }

    onFormSubmit1(gmt_offset) {
        this.setState({ gmt_offset1: gmt_offset});
    }
    onFormSubmit2(gmt_offset) {
        this.setState({ gmt_offset2: gmt_offset});
    }

    render() {
        return (
            <div>
                <TimeZoneSelector title="First timezone" onSubmit={this.onFormSubmit1} />
                <TimeZoneSelector title="Second timezone" onSubmit={this.onFormSubmit2} />
                <Textceptions str1={this.state.date1.format('HH:mm:ss')} str2={this.state.date2.format('HH:mm:ss')} />
            </div>
        )
    }
}